body {
  font-family: '굴림',Gulim;
  margin: 0;
  padding: 0;
  background: url("back6.png") no-repeat top fixed;
  background-size: cover;
  
  color: #f5e4c3;
}

body::-webkit-scrollbar {
  display: none;
}

.app{
  padding: 20px;
  margin: 20px auto;
  background: rgba(20, 20, 20, 0.836);
  border: 4px solid #b39260;
  border-radius: 8px;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.692);
}

.app {
  min-width: 576px;
  max-width: 1000px;
  margin-top: 100px;
}

.calculator-container {
  max-width: 600px;
  padding: 20px;
  margin: 20px auto;
}
.stopwatch-container {
  max-width: 600px;
  padding: 20px;
  margin: 20px auto;
}


button, .tab-button, .region-button, .dungeon-button, .update-log-button, .mode-buttons button, .action-section button {
  padding: 7px 9px;
  font-size: 14px;
  font-family: '굴림',Gulim;
  border: 2px solid #8d6346;
  background-color: #3b2b20;
  color: #f5e4c3;
  cursor: pointer;
  border-radius: 4px;
  transition: all 0.3s ease;
  text-shadow: 1px 1px 0 #4b3820;
}

button:hover, .tab-button:hover, .region-button:hover, .dungeon-button:hover, .update-log-button:hover, .mode-buttons button:hover, .action-section button:hover {
  background-color: #e46a5c;
  color: #fff;
}

button.active, .tab-button.active, .region-button.active, .dungeon-button.active, .mode-buttons button.active {
  background-color: #b34234;
  color: #fff;
  border-color: #e2a572;
}

.toggle-app-button {
  position: fixed;
  top: 10px;
  right: 10px;
  padding: 10px 15px;
  font-size: 16px;
  font-family: '굴림',Gulim;
  background-color: #3b2b20; /* 어두운 갈색 배경 */
  color: #f5e4c3; /* 고풍스러운 글자 색상 */
  border: 2px solid #8d6346; /* 테두리 */
  border-radius: 7px;
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 999; /* 다른 요소 위에 나타나도록 */
}

.tab-buttons {
  position: fixed;
  top: 10px;
  right: 70px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.tab-button {
  width: 120px;
  font-size: 13px;
}

.title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: bold;
  color: #e8c374;
  text-shadow: 2px 2px 0 #4b3820;
}

hr {
  border: 0;
  border-top: 2px solid #8d6346;
  margin: 10px 10px;
}

.region-buttons, .dungeon-buttons, .mode-buttons {
  display: flex;
  gap: 11px;
  justify-content: center;
  margin-bottom: 20px;
}

.debuffs {
  margin-top: 20px;
  text-align: center;
  color: #f5e4c3; /* 고풍스러운 글자 색상 */
  font-size: 14px;
  font-weight: 500;
}

.debuff-label {
  display: block;
  margin-bottom: 10px;
}

.monster-info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.monster-card {
  padding: 1px;
  background-color: #3b2b20;
  border-radius: 8px;
  border: 2px solid #8d6346;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.monster-image {
  width: 90px;
  height: 90px;
  object-fit: contain;
  margin-bottom: 10px;
  border-radius: 5px;
}

.monster-card ul {
  list-style: none;
  padding: 0;
}

.monster-card h4 {
  font-size: 16px;
  margin-bottom: 10px;
  color: #e8c374;
}

.monster-card li {
  font-size: 14px;
  margin-bottom: 5px;
}

.google-sheet-link {
  font-size: 16px;
  text-decoration: none;
  margin-right: 10px;
  color: #e8c374;
  transition: color 0.3s ease;
}

.google-sheet-link:hover {
  color: #fff;
}

.update-log, .main-log {
  margin-bottom :12px;
  padding: 15px;
  background-color: #251f1ab2;
  border: 2px solid #8d6346;
  border-radius: 8px;
  line-height: 1.5;
  font-size: 14px;
  color: #f5e4c3;
  white-space: pre-wrap;
}

.input-grid, .exp-inputs {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  margin-bottom: 20px;
  text-align: left;
}

.input-grid label, .exp-inputs label {
  font-size: 15px;
  color: #e8c374;
  text-shadow: 1px 1px 0 #4b3820;
  display: flex;
  align-items: center;
  height: 100%;
}

.input-grid input, .exp-inputs input, .time-exp input {
  min-width: 30%;
  padding: 8px;
  font-size: 14px;
  border: 2px solid #8d6346;
  border-radius: 4px;
  background-color: #3b2b20;
  color: #f5e4c3;
  text-align: center;
}

.input-grid input::placeholder, .exp-inputs input::placeholder {
  color: #c4a484;
}

.mode-buttons{
  display: flex;
  justify-content: start;
  gap: 10px;
  margin-bottom: 15px;
}

.action-section, .exp-result {
  margin-top : 4px;
  display: flex;
  justify-content: start;
  font-size: 16px;
}

/* 스핀 버튼 제거 */
input[type="number"] {
  -moz-appearance: textfield;
  appearance: textfield;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.stopwatch-display {
  
  font-size: 32px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
  color: #e8c374;
}

.region-buttons button:disabled {
  background-color: #5a4433;
  color: #aaa;
  cursor: not-allowed;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap : 10px;
}

